.who-sharing {
    /* variables */
    --color-bg: white;
}

.who-sharing {
    position: relative;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    margin: auto;
    padding: 20px;
    width: 300px;
    border: 1px solid black;
    border-radius: 20px;
    line-height: 2rem;
}

.who-sharing__item-name {
    box-sizing: border-box;
    border-bottom: 5px solid steelblue;
}
